import React from 'react'
import store from './index'
import Input from './components/Input'
import List from './components/List'

const App = () => {
    return(
        <div>
          <Input />
          <List listContent={['Mercury', 'Venus', 'Earth']}/>
          <List listContent={store.getState()}/>
        </div>
    )
}

export default App


